	import divider from '@/components/divider.vue';
	
	//引入网络请求
	import $H from '@/common/request.js';
	import $C from '@/common/config.js';
	import $U from '@/common/util.js';
	
	export default {
		components:{
			divider,
		},
		data() {
			return {
				background:{
					backgroundColor: '#fff',
				},
				typeIndex:2,
				is_verification:-1,
				page:1,
				show:false,
				reload: true,
				loadMore: false,
				status: 'more',
				contentText: {
					contentdown: '查看更多',
					contentrefresh: '加载中',
					contentnomore: '没有更多'
				},
				listData:[],
				nodata:false,
				scrollH:0
			}
		},
		
		
		onLoad() {
			 uni.getSystemInfo({
			 	success: (res) => {
					console.log(res.windowHeight)
					
					if(620<res.windowHeight<=688){
						this.scrollH=60
					}
					
			 		if(res.windowHeight<=688){
			 			this.scrollH=60
			 		}
					
					if(688<res.windowHeight<=730){
						this.scrollH=80
					}
					
			 		if(730<res.windowHeight<=810){
			 			this.scrollH=60
			 		}

                      if(810<res.windowHeight<=850){
			 			this.scrollH=80
			 		}
			 		
					 if(res.windowHeight>814){
						
						this.scrollH=80
					}
			 	}
			 });
			 this.getData()
		},
		
		onReachBottom() {
				this.loadMore = true;
				this.page++;  
				if (this.status == 'noMore') {
					return;
				}
				this.status = 'loading';
			    this.getData()
		},
		
		onPullDownRefresh() {
			this.reload=true;
			this.page=1;
			this.status = 'more';
			this.getData()
		},
		
		
		methods: {
			switchType(num){
				this.typeIndex=num;
				if(num==1){
				   this.is_verification=-1	
				   this.reload=true;
				   this.page=1;
				   this.status = 'more';
				   this.getData()
				}
				else if(num==2){
					this.is_verification=-1
					this.reload=true;
					this.page=1;
					this.status = 'more';
					this.getData()
				}
				else{
					 this.is_verification=1	
					 this.reload=true;
					 this.page=1;
					 this.status = 'more';
					 this.getData()
				}
			},
			
			getData(){
				let data={
					type:2,
					size:10,
					current:this.page,
					is_verification:this.is_verification
				}
				
				// 发送到服务端
				$H.get("/order/page/all",data).then((res) => {
					uni.stopPullDownRefresh()
					//请求成功
					console.log(res)
					let list = res.data.records
					
					let pages=res.data.pages;
					
					console.log(this.page)
					if(pages>1&&this.page<=pages){
						this.listData = this.reload ? list : this.listData.concat(list);
						this.nodata=false;
					}
					else if(pages==0){
						this.listData=[];
						this.nodata=true;
						this.status ='noMore'
						
					}
					else if(pages==1){
						this.listData = list
						this.status ='noMore'
						this.nodata=false;
					}
					else{
						this.status ='noMore'
					}
					
					this.reload = false;
					
					
				}).catch((e) => {
					uni.stopPullDownRefresh()
					//请求失败
					console.log("失败"+e)
				})
			},
			
			
			
			toOrderDetail(index){
				
				// console.log(this.listData[index])
				
				uni.navigateTo({
					url:'../orderDetail/orderDetail?data='+JSON.stringify(this.listData[index])
				})
			}
		}
	}
